<script>
    const demos = [
        {
            title: '',
            desc: '',
            code: `
                        <sw-button></sw-button>
                        <sw-button hollow ></sw-button>
                        <sw-button phStyle="link"></sw-button>`
        },
        {
            title: '',
            desc: 'phStyle primary',
            code: `
                        <sw-button phStyle="primary">primary</sw-button>
                        <sw-button phStyle="gray">gray</sw-button>
                        <sw-button phStyle="danger">danger</sw-button>
                        <sw-button phStyle="success">success</sw-button>
                        <sw-button phStyle="warning">warning</sw-button>
                        <sw-button phStyle="error">error</sw-button>
                        <sw-button phStyle="info">info</sw-button>`
        },
        {
            title: '',
            desc: 'hollow',
            code: `
                        <sw-button hollow>hollow</sw-button>
                        <sw-button hollow phStyle="gray">hollow</sw-button>
                        <sw-button hollow phStyle="success">hollow</sw-button>
                        <sw-button hollow phStyle="warning">hollow</sw-button>`
        },
        {
            title: '',
            desc: 'phSize  sm',
            code: `
                        <div><sw-button phSize="sm">sm</sw-button></div>
                        <div><sw-button phSize="md">md</sw-button></div>
                        <div><sw-button phSize="lg">md</sw-button></div>`
        },
        {
            title: '',
            desc: 'disabled',
            code: `
                        <sw-button disabled>disabled</sw-button>
                        <sw-button hollow disabled>disabled</sw-button>`
        },
        {
            title: '',
            desc: 'block',
            code: `
                        <sw-button block hollow></sw-button>
                        <sw-button block></sw-button>`
        },
        {
            title: '',
            desc: 'phIcon',
            code: `
                         <sw-button phIcon="loading-white"  phSize="lg" ></sw-button>
                         <sw-button phIcon="loading-white"  phSize="lg" stable></sw-button>`
        },
        {
            title: '',
            desc: '',
            code: `
                        <sw-button :phIcon="phIcon" block @click="handleClick">{{phIcon?'':''}}</sw-button>
                        <sw-button :phIcon="phIcon" block @click.native="handleClick">{{phIcon?'':''}}</sw-button>`,
            options:{
                data(){
                    return {
                        phIcon: ''
                    }
                },
                methods: {
                    handleClick: function(){
                        if(!this.phIcon){
                            this.phIcon = 'loading-white'
                            setTimeout(()=>{
                                this.phIcon = ''
                            },2000)
                        }
                    }
                }
            }

        }
    ]

    import Base from '../base.vue'
    export default {
        name: 'Button',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>

<style lang="less">
    .demo{
        .demo-button{
            margin: 0 .16rem .16rem 0;
        }
    }
</style>







