<script>
    const demos = [
        {
            title: '',
            desc: '',
            code: `<sw-input placeholder='...'/>`
        },
        {
            title: '',
            desc: 'disabled',
            code: `
                    <sw-input placeholder='...'
                              disabled />`
        },
        {
            title: '',
            desc: 'phIcon',
            code: `
                    <sw-input placeholder='...'
                              phIcon="search"/>`
        },
        {
            title: '',
            desc: 'visiblepassword',
            code: `
                    <sw-input type="password"
                              placeholder="..."
                              visible/>`
        },
        {
            title: '',
            desc: 'clear',
            code: `
                    <sw-input type="number"
                              :value="123"
                              placeholder='...'
                              clear/>`
        },
        {
            title: '',
            desc: 'error200',
            code: `
                    <sw-input   type="number"
                                placeholder="..."
                                :value="errorValue"
                                :error="isError"
                                @input="handleInput"/>`,
            options: {
                data(){
                    return {
                        isError: false,
                        errorValue: ''
                    }
                },
                methods: {
                    handleInput(value){
                        if(value > 200){
                            this.isError = true
                        }else{
                            this.isError = false
                        }
                    }
                }
            }
        },
        {
            title: '',
            desc: 'maxlength',
            code: `
                    <sw-input type="text"
                              placeholder='...'
                              :maxlength="3"/>`
        },
        {
            title: '',
            code: `
                    <span>{{value}}</span>
                    <sw-input type="text"
                              placeholder='...'
                              v-model="value"/>`,
            options: {
                data(){
                    return {
                        value : ''
                    }
                }
            }
        }
    ]
    import Base from '../base.vue'
    export default {
        name: 'Input',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>



