            `
        },
        {
            title: '-',
            desc: 'offset',
            code: `
                  <sw-grid>
                        <sw-row class="row-demo">
                            <sw-col width="25" ></sw-col>
                            <sw-col offset="10">10%</sw-col>
                        </sw-row>
                  </sw-grid>
            `
        }
    ]
    import Base from '../base.vue'
    export default {
        name: 'Grid',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>

<style lang="less">
    .col-demo-gray{
        background: #999;
    }
    .col-demo-color{
        background: #ff6633;
    }
    .row-demo .ph-col{
        background: #ff6633;
        margin-right: 0.1rem;
    }
    .row-demo{
        height: 1.5rem;
        background: #eee;
        color: #fff;
        text-align: center;
    }
    .col-1{
        height: 1rem;
        line-height: 1rem;
    }
    .col-2{
        height: 0.7rem;
        line-height: 0.7rem;
    }
    .col-3{
        height: 1.2rem;
        line-height: 1.2rem;
    }
</style>



