<script>
    const demos = [
        {
            title: '',
            code: `
                    <sw-list>
                        <sw-list-header></sw-list-header>
                        <sw-list-item>
                            <sw-list-col>
                                <sw-input type="text" value="" />
                            </sw-list-col>
                        </sw-list-item>
                    </sw-list>`
        },
        {
            title: '',
            desc: 'headingtail',
            code: `
                    <sw-list style="color: #fff;">
                        <sw-list-item>
                            <sw-list-col heading style="background: #ff6633">
                                <label>heading</label>
                            </sw-list-col>
                            <sw-list-col tail style="background: #bbb">
                                 <label>tail</label>
                            </sw-list-col>
                        </sw-list-item>
                        <sw-list-item>
                            <sw-list-col style="background: #ff6633">
                                <label>heading</label>
                            </sw-list-col>
                            <sw-list-col style="background: #bbb">
                                 <label> tail</label>
                            </sw-list-col>
                        </sw-list-item>
                    </sw-list>`
        },
        {
            title: '',
            desc: 'disabled',
            code: `
                    <sw-list>
                        <sw-list-item disabled>
                            <sw-list-col>
                                <sw-input type="text" value="" />
                            </sw-list-col>
                        </sw-list-item>
                    </sw-list>`
        },
        {
            title: 'Error',
            desc: 'error',
            code: `
                    <sw-list>
                         <sw-list-item error>
                            <sw-list-col heading>
                                <label></label>
                            </sw-list-col>
                            <sw-list-col>
                                <sw-input type="text" placeholder="" value="error" clear error/>
                            </sw-list-col>
                        </sw-list-item>
                    </sw-list>`
        },
        {
            title: '',
            desc: 'required',
            code: `
                    <sw-list>
                          <sw-list-item required>
                            <sw-list-col>
                                <sw-input type="text" placeholder=""/>
                            </sw-list-col>
                          </sw-list-item>
                    </sw-list>`
        },
        {
            title: '',
            desc: 'navigate',
            code: `
                    <sw-list>
                          <sw-list-item navigate>
                            <sw-list-col heading>
                                <label></label>
                            </sw-list-col>
                            <sw-list-col tail>
                                <sw-input type="text" p
                                          placeholder="/"
                                          @click.native="handleClick"
                                          disabled/>
                            </sw-list-col>
                        </sw-list-item>
                    </sw-list>`,
            options: {
                methods: {
                    handleClick(){
                        window.open('https://cn.vuejs.org/v2/api/#v-on','_blank')
                    }
                }
            }
        }
    ]
    import Base from '../base.vue'
    export default {
        name: 'List',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>


