<script>
 const demos = [
     {

         title: '',
         desc: 'closeButton\nshadowDisabled\n on-close\n',
         code: `
                    <sw-button @click="visible = true"></sw-button>
                    <sw-dialog title=""
                               v-model="visible"
                               shadowDisabled
                               closeButton>
                        
                    </sw-dialog>`,
         options: {
             data(){
                 return {
                     visible: false
                 }
             }
         }
     },
     {

         title: '',
         desc: 'slottitledefaultfooter',
         code: `
                    <sw-button @click="visible = true"></sw-button>
                    <sw-dialog v-model="visible"
                               closeButton>
                        <div slot="title"><span style="color: #ff6633"></span></div>
                        <div><span style="color: #ff6633">1000</span><br/></div>
                        <div slot="footer">
                            <sw-button-group>
                                <sw-button phSize="md"
                                           hollow
                                           @click="visible = false"></sw-button>
                                <sw-button hollow phSize="md" @click="handleConfirm"></sw-button>
                            </sw-button-group>
                        </div>
                    </sw-dialog>`,
         options: {
             data(){
                 return {
                     visible: false
                 }
             },
             methods: {
                 handleConfirm(){
                     window.alert('I am sure!!')
                     this.visible = false
                 }
             }
         }
     }
   ]
   import Base from '../base.vue'
   export default {
       name: 'Dialog',
       extends:Base,
       data(){
           return {
               demos: demos
           }
       }
   }
</script>


