<script>
    const demos = [
        {
            title: '',
            code: '<sw-textarea placeholder="..." />'
        },
        {
            title: '',
            desc: 'count  maxlength ',
            code: `
                   <sw-textarea count
                                :maxlength="10"
                                placeholder="..." />`
        },
        {
            title: 'change',
            desc: 'count  maxlength ',
            code: `
                   <sw-textarea :value="content"
                                :count="true"
                                :maxlength="10"
                                placeholder="..."
                                @change="handleChange" />`,
            options: {
                data(){
                    return {
                        content: ''
                    }
                },
                methods: {
                    handleChange(val){
                        this.content = val
                        alert(val)
                    }
                }
            }
        }
    ]
    import Base from '../base.vue'
    export default {
        name: 'Textarea',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>





