<script>
    const demos = [
        {
            title: '',
            desc: 'align,top',
            code: `
                <sw-button @click="visible=true">top</sw-button>
                <sw-popup v-model="visible">
                    <div class='ph-action-sheet'>
                        <ul>
                            <li class='ph-popup-item ph-popup-tip'> </li>
                            <li class='ph-popup-item ph-popup-delete' @click="visible=false"></li>
                        </ul>
                        <div class='ph-popup-operate' @click="visible=false"></div>
                    </div>
                </sw-popup>`,
            options: {
                data(){
                    return {
                        visible: false
                    }
                }
            }
        },
        {
            title: '',
            desc: 'align,bottom',
            code: `
                <sw-button @click="visibleBottom=true">bottom</sw-button>
                <sw-popup v-model="visibleBottom" align="bottom">
                    <div class='ph-action-sheet'>
                        <ul>
                            <li class='ph-popup-item ph-popup-tip'> </li>
                            <li class='ph-popup-item ph-popup-delete' @click="visibleBottom = false"></li>
                        </ul>
                        <div class='ph-popup-operate' @click="visibleBottom = false"></div>
                    </div>
                </sw-popup>`,
            options: {
                data(){
                    return {
                        visibleBottom: false
                    }
                }
            }
        }
    ]
    import Base from '../base.vue'
    export default {
        name: 'Popup',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>

<style>
    .demo .ph-popup-main{
        margin-top: 1.2rem;
    }
</style>


