<script>
    const demos = [
        {
            title: '',
            desc: '',
            code: `
            <sw-button-group>
                <sw-button hollow phStyle="gray"></sw-button>
                <sw-button></sw-button>
            </sw-button-group>`
        },
        {
            title: '',
            desc: 'phTypejustify',
            code: `
            <sw-button-group phType="justify">
                <sw-button :hollow="active1 != 'button1'" @click="active1='button1'"></sw-button>
                <sw-button :hollow="active1 != 'button2'" @click="active1='button2'"></sw-button>
            </sw-button-group>`,
            options: {
                data(){
                    return { active1: 'button1'}
                }
            }
        },
        {
            title: '',
            desc: 'phTypesegmente',
            code: `
             <sw-button-group phType="segmente" class="space">
                <sw-button id="button7" v-bind:hollow="active3 != 'button7'" @click="active3 = 'button7'"></sw-button>
                <sw-button id="button8" v-bind:hollow="active3 != 'button8'" @click="active3 = 'button8'"></sw-button>
                <sw-button id="button9" v-bind:hollow="active3 != 'button9'" @click="active3 = 'button9'"></sw-button>
             </sw-button-group>`,
            options: {
                data(){
                    return { active3: 'button7'}
                }
            }
        },
        {
            title: '',
            desc: 'phType tacked',
            code: `
             <sw-button-group phType="tacked">
                <sw-button block :hollow="active2 != 'button4'" @click="active2='button4'"></sw-button>
                <sw-button block :hollow="active2 != 'button5'" @click="active2='button5'"></sw-button>
                <sw-button block :hollow="active2 != 'button6'" @click="active2='button6'"></sw-button>
             </sw-button-group>`,
            options: {
                data(){
                    return { active2: 'button5'}
                }
            }
        }
    ]
    import Base from '../base.vue'
    export default {
        name: 'ButtonGroup',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>

<style>
    .space {
        font-size: 0;
    }
</style>






