<script>
    const demos = [
        {
            title: '',
            desc: '',
            code: `
                <sw-button @click="visible1 = true">Alert</sw-button>
                <sw-alert v-model="visible1"
                          title=""
                          content=""
                          shadowDisabled></sw-alert>`,
            options: {
                data(){
                    return {
                        visible1: false
                    }
                }
            }
        },
        {
            title: 'Alert',
            desc: '',
            code: `
                <sw-button @click="visible2 = true">Alert</sw-button>
                <sw-alert v-model="visible2"
                          title=""
                          content=""
                          :buttons="buttons"></sw-alert>`,
            options: {
                data(){
                    return {
                        visible2: false,
                        buttons: [
                            {text: '', phStyle: 'gray', otherProps: {hollow: true}},
                            {onHandle: this.handleConfirm}
                        ]
                    }
                },
                methods: {
                    handleConfirm(){
                        alert('')
                        this.visible2 = false
                    }
                }
            }
        },
        {
            title: 'Alert',
            desc: '',
            code: `
                <sw-button @click="visible3 = true">Alert</sw-button>
                <sw-alert v-model="visible3"
                          title=""
                          content=""
                          :buttons="buttons3"
                          closeButton></sw-alert>`,
            options: {
                data(){
                    return {
                        visible3: false,
                        buttons3: [
                            {text: '1',  otherProps: {hollow: true}, onHandle:()=>{alert('1,');this.visible3=false}},
                            {text: '2',  otherProps: {hollow: true}, onHandle:()=>{alert('2')}},
                            {text: '3',  otherProps: {hollow: true}, onHandle:()=>{alert('3')}}
                        ]
                    }
                }
            }
        }
    ]
    import Base from '../base.vue'
    export default {
        name: 'Alert',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>

<style>
    @import "../demo.less";
</style>



