<template>
<div>
    <h3 class="comp-type"><strong>sw-tab-set</strong></h3>
    <h3 class="comp-type">defaultActiveIndex() </h3>
    <sw-tab-set defaultActiveIndex="1">
        <sw-tab heading="1" :index="1">
            1
        </sw-tab>
        <sw-tab heading="2" :index="2">
            2
        </sw-tab>
    </sw-tab-set>

    <h3 class="comp-type">vertical(false) </h3>
    <sw-tab-set vertical>
        <sw-tab heading="1"  :index="1">
            <sw-list>
                <sw-list-item>
                    <sw-list-col>1</sw-list-col>
                </sw-list-item>
            </sw-list>
        </sw-tab>
        <sw-tab heading="2" :index="2">
            2
        </sw-tab>
        <sw-tab heading="3" :index="3">
            3
        </sw-tab>
    </sw-tab-set>
    <br/>

    <h3 class="comp-type">width(33) vertical</h3>
    <sw-tab-set vertical width="20">
        <sw-tab heading="1" :index="1">
            <sw-list>
                <sw-list-item>
                    <sw-list-col>1</sw-list-col>
                </sw-list-item>
            </sw-list>
        </sw-tab>
        <sw-tab heading="2" :index="2">
            <sw-list>
                <sw-list-item>
                    <sw-list-col>2</sw-list-col>
                </sw-list-item>
            </sw-list>
        </sw-tab>
    </sw-tab-set>
    <br/>

    <h3 class="comp-type">tabsetChange(SwTabSet) </h3>
    <sw-tab-set @tab-change="tabsetChange">
        <sw-tab heading="1" :index="1">
            1
        </sw-tab>
        <sw-tab heading="2" :index="2">
            2
        </sw-tab>
    </sw-tab-set>

    <h3 class="comp-type"><strong>SwTab</strong></h3>
    <h3 class="comp-type">heading </h3>
    <sw-tab-set>
        <sw-tab heading="sw-tab heading 1" :index="1">
            1
        </sw-tab>
        <sw-tab heading="sw-tab heading 2" :index="2">
            2
        </sw-tab>
    </sw-tab-set>

    <h3 class="comp-type">tabChange(SwTab) </h3>
    <sw-tab-set>
        <sw-tab heading="1" @tab-change="tabChange" :index="1">
            1
        </sw-tab>
        <sw-tab heading="2" :index="2">
            2
        </sw-tab>
    </sw-tab-set>
</div>
</template>

<script>
    export default {
        name: 'tabset',
        data(){
            return {
            }
        },
        methods: {
            tabsetChange(index){
                alert("tabset index: " + index);
            },

            tabChange(index){
                alert("tab index: " + index);
            }

        }
    }
</script>



