<script>
    const demos = [
        {
            title: '',
            desc: 'on-confirm  ',
            code: `
                <div>{{text}}</div>
                <sw-button @click="visible = true">Prompt</sw-button>
                <sw-prompt v-model="visible"
                           title=""
                           content=""
                           shadowDisabled closeButton
                           @on-confirm="handleConfirm"></sw-prompt>`,
            options: {
                data(){
                    return {
                        visible: false,
                        text: ''
                    }
                },
                methods: {
                    handleConfirm(value){
                        if(!value){
                            alert('...')
                        }else{
                            this.text = value
                            this.visible = false
                        }
                    }
                }
            }
        },
        {
            title: '',
            code: `
                <div>{{text}}</div>
                <sw-button @click="visible = true">Prompt</sw-button>
                <sw-prompt v-model="visible"
                           title=""
                           content=""
                           shadowDisabled closeButton
                           :buttons="[{text: '', onHandle: handleConfirm}]"></sw-prompt>`,
            options: {
                data(){
                    return {
                        visible: false,
                        text: ''
                    }
                },
                methods: {
                    handleConfirm(value){
                        if(!value){
                            alert('...')
                        }else{
                            this.text = value
                            this.visible = false
                        }
                    }
                }
            }
        }
    ]
    import Base from '../base.vue'
    export default {
        name: 'Dialog',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>


