<script>
    const demos = [
        {
            title: '',
            code: `
            <sw-badge><span></span></sw-badge>
            <sw-badge phStyle="success"></sw-badge>
            <sw-badge phStyle="info"></sw-badge>
            <sw-badge phStyle="danger"></sw-badge>
            <sw-badge phStyle="error"></sw-badge>
            <sw-badge phStyle="warning"></sw-badge>`
        },
        {
            title: '',
            code: `
            <sw-badge>1</sw-badge>
            <sw-badge>2</sw-badge>
            <sw-badge>3</sw-badge>
            <sw-badge>4</sw-badge>
            <sw-badge>5</sw-badge>`
        },
        {
            title: '',
            code: `
            <sw-badge phStyle="danger">hot</sw-badge>
            <sw-badge phStyle="danger">new</sw-badge>`
        }
    ]

    import Base from '../base.vue'
    export default {
        name: 'Badge',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>



