<script>
    const demos = [
        {
            title: '',
            desc: '',
            code: '<sw-search></sw-search>'
        },
        {
            title: '',
            desc: 'buttonText',
            code: '<sw-search buttonText=""></sw-search>'
        },
        {
            title: 'click',
            desc: 'clickblurfocus',
            code: `
                <sw-search v-model="searchValue"
                           @click="handleClick"
                           buttonText="">
                </sw-search>
            `,
            options: {
                data(){
                    return {
                        searchValue: ''
                    }
                },
                methods: {
                    handleClick(){
                        this.searchValue = ''
                    }
                }
            }
        },
        {
            title: 'search',
            desc: 'search',
            code: `
                <sw-search @search="handleSearch"
                           @click="handleSearch"
                           buttonText="">
                </sw-search>
            `,
            options: {
                methods: {
                    handleSearch(val){
                        alert(`${val}`)
                    }
                }
            }
        }
    ]
    import Base from '../base.vue'
    export default {
        name: 'Search',
        extends:Base,
        data(){
            return {
                demos: demos
            }
        }
    }
</script>


